.quiz-section {
    padding: 180px 0 60px;

    &__wrapp {
        position: relative;
        padding: 96px 110px 144px;
        display: flex;
        flex-direction: column;
        gap: 32px;
        background: linear-gradient(180deg, #f6e2b7 0%, #fade97 100%);
        border-radius: 64px;
        box-shadow: 16px 30px 76px 0 rgba(0, 0, 0, 0.05), 64px 122px 137px 0 rgba(0, 0, 0, 0.04), 144px 274px 186px 0 rgba(0, 0, 0, 0.03), 256px 486px 220px 0 rgba(0, 0, 0, 0.01), 400px 760px 240px 0 rgba(0, 0, 0, 0);
    }

    &__text {
        font-family: var(--font-primary);
        font-size: 26px;
        line-height: 154%;
        font-weight: 600;
        letter-spacing: -0.02em;
    }

    &__variants {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 32px;
    }

    &__more {
        margin: 0 auto;
        padding: 14px 56px;
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 18px;
        line-height: 167%;
        font-weight: 500;
        letter-spacing: -0.02em;
        color: rgba(var(--cl-e),1.0);
        background: linear-gradient(180deg, #fff 0%, #fafafa 100%);
        border-radius: 100px;
    }
}